<script>
	import {wxAuOpenId,wxAuthorize} from "@/common/js/app.js"
	export default {
		onLaunch: function() {
			// #ifdef H5
				// 如果存在startTimeApp  86400000 24小时
				const startTime=uni.getStorageSync("startTimeApp");
				// 设置结束的时间
				const endTime=new Date().getTime();
				if(!startTime){
					const startTime=new Date().getTime();
					uni.setStorageSync("startTimeApp",startTime);
				}else if(Number(endTime)-Number(startTime)>=86400000){
					uni.clearStorageSync();
					uni.setStorageSync("startTimeApp",endTime);
				}
				
				// 是否触发
				const openId=uni.getStorageSync("openId");
				const userId=uni.getStorageSync("userId");
				
				if(!openId && !userId){
					wxAuthorize();
				}else{
					wxAuOpenId(openId);
				}
			// #endif
		},
	}
</script>

<style lang="scss">
	/*每个页面公共css */
	@import './common/css/common.css';
	/* uview ui */
	@import "uview-ui/index.scss";
</style>
